<template>
	<view class="content">
		<image class="logo" src="/static/logo.png"></image>
		<view class="text-area">
			<text class="title">{{title}}</text>
		</view>
		
		<view>
			<u-toast ref="uToast"/>
		</view>

		<u-form :model="form" ref="uForm" class="my-form">
			<u-form-item :leftIconStyle="{color: '#888', fontSize: '32rpx'}" left-icon="account-fill" label-width="120"
			 :label-position="labelPosition" label="手机" prop="mobile">
				<u-input :border="border" placeholder="请输入手机号" v-model="form.mobile" type="text"></u-input>
			</u-form-item>
			<u-form-item :leftIconStyle="{color: '#888', fontSize: '32rpx'}" left-icon="lock-fill" label-width="120"
			 :label-position="labelPosition" label="密码" prop="password">
				<u-input :password-icon="true" :border="border" type="password" v-model="form.password" placeholder="请输入密码"></u-input>
			</u-form-item>
		
			<button class="loginStyle" open-type="getUserInfo" @getuserinfo="getUserInfo">登录</button>
			<button class="loginStyle" open-type="getUserInfo" @getuserinfo="registerInfo">注册</button>
			
		</u-form>

	</view>
</template>

<script>
	import {
		wxLogin, CMD_LOGIN, CMD_REGISTER
	} from '@/services/api.js';
	
	import md5Libs from "uview-ui/libs/function/md5";
	
	export default {
		data() {
			return {
				title: '请登录',
				value: '',
				type: 'text',
				border: true,
				pValue: '',
				ptype: 'password',
				passwordIcon: true,
				pborder: true,
				form: {
					mobile: '',
					password: '',
					loginType: 'customer'
				},
			}
		},
		onLoad() {

		},
		methods: {
			showToast(){
				this.$refs.uToast.show({
									title:'登录成功',
									type:'success',
									url:'/pages/itemlist/itemlist'
				})
			},
			getUserInfo(res) {
				console.log('login', res)
				this.doLogin(CMD_LOGIN);
			},
			registerInfo(res) {
				console.log('in register info');
				this.doLogin(CMD_REGISTER);
			},
			doLogin(cmdType) {
				let that = this;
				uni.login({
						provider: 'weixin',
						success: function(loginRes) {
							// that.switchPage()
					
							if (loginRes.code !== null && loginRes.code !== '') {
								uni.showLoading({
								    title: '登录中...'
								});
								wxLogin({
									authCode: loginRes.code,
									cmdType: cmdType,
									mobile: that.form.mobile,
									password: that.form.password
								}).then(res => {
									console.log('res is ' + res);
									that.showToast();
									uni.redirectTo({
										url: '/pages/itemList/itemList'
									});
								}).finally(() => {
									uni.hideLoading()
								})
							}
						}
					});
				
			}
		}
	}
</script>

<style>
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.logo {
		height: 200rpx;
		width: 200rpx;
		margin-top: 200rpx;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 50rpx;
	}

	.text-area {
		display: flex;
		justify-content: center;
	}

	.title {
		font-size: 36rpx;
		color: #8f8f94;
	}
	
	.my-form {
		width: 79.37%;
		text-align: center;
		vertical-align: middle;
		margin: 0 auto;
		display: inline-block;
		position: relative;
		top: 235rpx;
	}
	
	.loginStyle {
		background-color: #ea810c;
		color: #fff;
		border-radius: 100px;
		margin-top: 150upx;
	}

</style>
